import {
	mapState,
	mapMutations
} from 'vuex'
import {
	getAvailableMoney
} from '@/api/index.js'

export default {
			
	data() {
		return {
			availableMoney: 0,
		}
	},
	filters: {
		toMoney: function(value) {
			if (typeof(value) == 'number') {
				return `￥${value.toFixed(2)}`;
			} else {
				return "￥0.00"
			}
		}
	},
	computed: {
		...mapState(['hasLogin', 'forcedLogin','admin','userName'])
	},
	methods: {
		...mapMutations(['logout']),
		bindLogin() {
			uni.navigateTo({
				url: '../login/login',
			});
		},
		redirect(name) {
			if (name == 'withdraw') {
				uni.navigateTo({
					url: '/pages/withdraw/withdraw'
				})
			} else if (name == 'postadmin') {
				uni.navigateTo({
					url: '/pages/postadmin/postadmin'
				})
			} else if (name == 'newtask') {
				uni.navigateTo({
					url: '/pages/newtask/newtask'
				});
			} else if (name == 'children') {
				uni.navigateTo({
					url: '/pages/children/children'
				});
			}
		},

		async getItem() {
			this.$$.showLoading();
			try {
				const data = await getAvailableMoney();
				this.availableMoney = data.balance;
			} catch (e) {
				console.error(e);
				this.$$.showErr();
			}
			this.$$.hideLoading()

		},

		bindLogout() {
			this.logout();
			/**
			 * 如果需要强制登录跳转回登录页面
			 */
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../login/login',
				});
			}
		}
	},
	onLoad() {
		if (!this.hasLogin) {
			uni.showModal({
				title: '未登录',
				content: '您未登录，需要登录后才能继续',
				/**
				 * 如果需要强制登录，不显示取消按钮
				 */
				showCancel: !this.forcedLogin,
				success: (res) => {
					if (res.confirm) {
						/**
						 * 如果需要强制登录，使用reLaunch方式
						 */
						if (this.forcedLogin) {
							uni.reLaunch({
								url: '../login/login'
							});
						} else {
							uni.navigateTo({
								url: '../login/login'
							});
						}
					}
				}
			});
		} else {
			this.getItem();
		}
	}
}
